    monospace;
}

main{width:100vw;height:100vh;max-width:100vw;max-height:100vh;overflow:scroll;font-family:"Overpass",sans-serif}nav>ul{display:flex;justify-content:center;padding:0}nav>ul li{list-style:none;max-width:50px}nav>ul a{color:transparent}nav>ul a[aria-label=home]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_home.e8053332.svg)}nav>ul a[aria-label=about]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_about.057c7e28.svg)}nav>ul a[aria-label=work]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_work.a01e0df5.svg)}nav>ul a[aria-label=work]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_work.a01e0df5.svg)}nav>ul a[href="https://github.com/lemamon"]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_github.16a9304e.svg)}nav>ul a[href="https://linkedin.com/in/lemamon"]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_linkedin.03f1d164.svg)}nav>ul a[href="https://instagram.com/lemamon"]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_instagram.f0cfa30d.svg)}nav>ul a[href="https://twitter.com/lemamon"]::before{content:"";width:36px;height:36px;position:absolute;background-image:url(/lemamon/static/media/ic_twitter.e7886f46.svg)}header>nav>ul{position:absolute;align-items:center;width:100%;height:65px;background:#fff;margin:0}footer{position:fixed;bottom:10px;min-width:100vw}@media(min-width: 1024px){footer nav>ul{justify-content:flex-end}}section{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%}.home{padding:20px 60px 10px}.home .title-container{display:flex;flex-direction:column;justify-content:left;width:100%}.home .title{width:187px;font-